		testAmtProfit(rounds) {
			let sumIncomeWin = 0
			let sumIncomeLose = 0
			let sumTestIncomeWin = 0
			let sumTestIncomeLose = 0
			let sumTest2IncomeWin = 0
			let sumTest2IncomeLose = 0

			for (let item of rounds) {
				// if (item.epoch > 58490) {
				let fee = 0.00047

				let testAmt = Number(this.amts_test1[item.position_index - 1])
				let testAmt2 = Number(this.amts_test2[item.position_index - 1])
				if (isNaN(testAmt)) {
					testAmt = 0
				}

				if (isNaN(testAmt2)) {
					testAmt2 = 0
				}

				if (item.up_down === item.position && item.amt > 0) {
					let payout = 0

					if (item.up_down === 'UP') {
						item.payout = item.bear_amt / item.bull_amt + 1
						payout = item.bear_amt / item.bull_amt + 1
					} else {
						item.payout = item.bull_amt / item.bear_amt + 1
						payout = item.bull_amt / item.bear_amt + 1
					}

					item.income = item.amt * payout * 0.97 - item.amt
					let testIncome1 = testAmt * payout * 0.97 - testAmt
					let testIncome2 = testAmt2 * payout * 0.97 - testAmt2

					if (item.income > 0) item.income += fee
					if (testIncome1 > 0) testIncome1 += fee
					if (testIncome2 > 0) testIncome2 += fee

					sumIncomeWin += item.income
					sumTestIncomeWin += testIncome1
					sumTest2IncomeWin += testIncome2

					// console.log(item.epoch,' :',testIncome2)

					if (item.amt > 0) {
						this.summary.sumPayout += payout * 0.97
						this.summary.payoutCount++
					}
				} else if (item.amt > 0) {
					item.income = -item.amt
					// console.log(item.income)
					let testIncome1 = -testAmt
					let testIncome2 = -testAmt2

					if (item.income > 0) item.income += fee
					if (testIncome1 > 0) testIncome1 += fee
					if (testIncome2 > 0) testIncome2 += fee

					// console.log(item.epoch,' :',testIncome2)

					sumIncomeLose += item.income
					sumTestIncomeLose += testIncome1
					sumTest2IncomeLose += testIncome2
				}

				// }
			}

			this.summary.sumIncomeWin = sumIncomeWin
			this.summary.sumTestIncomeWin = sumTestIncomeWin
			this.summary.sumTest2IncomeWin = sumTest2IncomeWin

			this.summary.sumIncomeLose = sumIncomeLose
			this.summary.sumTestIncomeLose = sumTestIncomeLose
			this.summary.sumTest2IncomeLose = sumTest2IncomeLose

			console.log(this.summary.sumTestIncomeWin, this.summary.sumTestIncomeLose)
		},

		testPositionReverse(rounds) {
