					let frame = this.getFrame(rounds, k + 2, 20)
					let findItem1 = []
					let targetEpoch = rounds[k].epoch % 2

					for (let item of frame) {
						if (
							item &&
							(item.position_rsi === 'UP' || item.position_rsi === 'DOWN') &&
							item.epoch % 2 === targetEpoch
						) {
							findItem1.push(item)
						}
					}

					findItem1 = findItem1[0]

					if (findItem1) {
						if (findItem1.up_down === findItem1.position_rsi) {
							rounds[k].position_index_reverse = 1
						} else {
							rounds[k].position_index_reverse = findItem1.position_index_reverse + 1
						}
					}
				}
			}

			for (let i = rounds.length - 3; i >= 0; i--) {
				let fee = 0.00047

				if (rounds[i].position_reverse === 'UP' || rounds[i].position_reverse === 'DOWN') {
					let tempAmt = this.amts_test1[rounds[i].position_index_reverse - 1]

					if (rounds[i].up_down === rounds[i].position_reverse) {
