						} else {
							payout = rounds[i].bull_amt / rounds[i].bear_amt + 1
						}

						let testIncome2 = tempAmt * payout * 0.97 - tempAmt
						if (testIncome2 > 0) testIncome2 += fee

						if (tempAmt !== undefined) {
							sumTestPositionEmaWin += testIncome2
							rounds[i].amt_test = testIncome2
						}

						// console.log(rounds[i].epoch,' :',testIncome1)
					} else {
						let testIncome2 = -tempAmt
						if (testIncome2 > 0) testIncome2 += fee
						// console.log(rounds[i].epoch, ' :', rounds[i].position_reverse, ' , ', testIncome2, ' amt:', tempAmt)

						if (tempAmt !== undefined) {
							sumTestPositionEmaLose += testIncome2
							rounds[i].amt_test = testIncome2
						}
					}
				}

				if (rounds[i].position_rsi !== 'SKIP') {
					if (rounds[i].up_down === rounds[i].position_rsi) {
						let objKey = 'i' + rounds[i].position_index_reverse
						this.summary.winCount[2][objKey]++
					} else {
						let objKey = 'i' + rounds[i].position_index_reverse
						this.summary.winCount[3][objKey]++
					}
				}
			}

			this.summary.sumPositionEmaWin = sumTestPositionEmaWin
			this.summary.sumPositionEmaLose = sumTestPositionEmaLose

			return rounds
		},

