				let fee = 0.00047

				if (rounds[i].position_down === 'UP' || rounds[i].position_down === 'DOWN') {
					let tempAmt = this.amts_test1[rounds[i].position_index_down - 1]

					if (rounds[i].up_down === rounds[i].position_down) {
						let payout = 0
						if (rounds[i].up_down === 'UP') {
							payout = rounds[i].bear_amt / rounds[i].bull_amt + 1
						} else {
							payout = rounds[i].bull_amt / rounds[i].bear_amt + 1
						}

						let testIncome2 = tempAmt * payout * 0.97 - tempAmt
						if (testIncome2 > 0) testIncome2 += fee

						if (tempAmt !== undefined) {
							sumTestPositionShortWin += testIncome2
							rounds[i].amt_test = testIncome2
						}

						// console.log(rounds[i].epoch,' :',testIncome1)
					} else {
						let testIncome2 = -tempAmt
						if (testIncome2 > 0) testIncome2 += fee
						// console.log(rounds[i].epoch, ' :', rounds[i].position_down, ' , ', testIncome2, ' amt:', tempAmt)

						if (tempAmt !== undefined) {
							sumTestPositionShortLose += testIncome2
							rounds[i].amt_test = testIncome2
						}
					}
				}

				if (rounds[i].position_down !== 'SKIP') {
					if (rounds[i].up_down === rounds[i].position_down) {
						let objKey = 'i' + rounds[i].position_index_down
						this.summary.winCount[4][objKey]++
					} else {
						let objKey = 'i' + rounds[i].position_index_down
						this.summary.winCount[5][objKey]++
					}
				}
			}

			this.summary.sumPositionDownWin = sumTestPositionShortWin
			this.summary.sumPositionDownLose = sumTestPositionShortLose

			return rounds
		},

		getFrame(rounds, start, size) {
			let list = []
			for (let i = start; i < start + size; i++) {
				list.push(rounds[i])
			}
			return list
		},

		testLatest3WinIndex(rounds) {
			for (let i = rounds.length - 3; i >= 0; i--) {
				if (rounds[i].position_index_long === 1) {
					if (rounds[i].up_down === rounds[i].position_long) {
						rounds[i].position1_win_index = 1
					} else {
						if (rounds[i].status !== 'SKIP') {
							rounds[i].position1_win_index = 0
						}
					}
				}
			}

			return rounds
		},

		async setAmts() {
			this.amts_test1 = []
			this.amts_test2 = []
			this.amts_actual = []

			let amts = await HttpService.get('/pish/prediction/amts')
			for (let item of amts) {
				if (item.name === 'index2') {
					this.amts_actual.push(item.i1)
					this.amts_actual.push(item.i2)
					this.amts_actual.push(item.i3)
					this.amts_actual.push(item.i4)
					this.amts_actual.push(item.i5)
					this.amts_actual.push(item.i6)
					this.amts_actual.push(item.i7)
					this.amts_actual.push(item.i8)
				} else if (item.name === 'index2_test1') {
					this.amts_test1.push(item.i1)
					this.amts_test1.push(item.i2)
					this.amts_test1.push(item.i3)
					this.amts_test1.push(item.i4)
					this.amts_test1.push(item.i5)
					this.amts_test1.push(item.i6)
					this.amts_test1.push(item.i7)
					this.amts_test1.push(item.i8)
				} else if (item.name === 'index2_test2') {
					this.amts_test2.push(item.i1)
					this.amts_test2.push(item.i2)
					this.amts_test2.push(item.i3)
					this.amts_test2.push(item.i4)
					this.amts_test2.push(item.i5)
					this.amts_test2.push(item.i6)
					this.amts_test2.push(item.i7)
					this.amts_test2.push(item.i8)
				}
			}
		},

		async getSettings() {
			let settings = await HttpService.get('/pish/prediction/rounds2/settings')
			this.settings = settings[0]
			console.log(settings)
		},

		clickAmtTest() {
			this.testAmtProfit(this.rounds)
			this.testPositionLong(this.rounds)
			this.testPositionShort(this.rounds)
		},

		async clickUpdateSetting() {
			await HttpService.put('/pish/prediction/rounds2/settings', this.settings)
		},

		async clickUpdateLong() {
			this.settings.long_short = 1
			await HttpService.put('/pish/prediction/rounds2/settings/longshort', this.settings)
		},

		async clickUpdateShort() {
			this.settings.long_short = 0
			await HttpService.put('/pish/prediction/rounds2/settings/longshort', this.settings)
		},

		async clickApplyTest(nameNum, amtTest) {
			let numList = amtTest.map((x) => Number(x))
			let name = ''
			if (nameNum === 1) {
				name = 'index2_test1'
			} else if (nameNum === 2) {
				name = 'index2_test2'
			}

			await HttpService.put('/pish/prediction/amts', { name: name, amt_list: numList })
			this.setAmts()
		},

		async clickUpdateEma(nameNum, _ema1, _ema2) {
			let name = 'index2'
			await HttpService.put('/pish/prediction/settings/ema', { name: name, ema1: _ema1, ema2: _ema2 })
		},

		getPercent(total, count) {
			return ((count / total) * 100).toFixed(1)
		},

		winColor(up_down, index, position, status) {
			let color = 'white lighten-3'

			if (up_down === position) {
				if (up_down === 'UP') {
					if (index >= 6) {
						color = 'green lighten-2'
					} else if (index >= 5) {
						color = 'green lighten-3'
					} else if (index >= 4) {
						color = 'green lighten-4'
					} else if (index >= 1) {
						color = 'green lighten-5'
					}
				} else {
					if (index >= 6) {
						color = 'red lighten-2'
					} else if (index >= 5) {
						color = 'red lighten-3'
					} else if (index >= 4) {
						color = 'red lighten-4'
					} else if (index >= 1) {
						color = 'red lighten-5'
					}
				}
			} else {
				// if (index == 1) {
				// color = 'blue lighten-4'
				// }
			}
			return color
		},

		winFinalColor(up_down, index, up, down, status) {
			let color = 'white lighten-3'

			if (up_down === up) {
				if (up_down === 'UP') {
					if (index >= 6) {
						color = 'green lighten-2'
					} else if (index >= 5) {
						color = 'green lighten-3'
					} else if (index >= 4) {
						color = 'green lighten-4'
					} else if (index >= 1) {
						color = 'green lighten-5'
					}
				} else {
					if (index >= 6) {
						color = 'red lighten-2'
					} else if (index >= 5) {
						color = 'red lighten-3'
					} else if (index >= 4) {
						color = 'red lighten-4'
					} else if (index >= 1) {
						color = 'red lighten-5'
					}
				}
			} else if (up_down === down) {
				if (up_down === 'UP') {
					if (index >= 6) {
						color = 'green lighten-2'
					} else if (index >= 5) {
						color = 'green lighten-3'
					} else if (index >= 4) {
						color = 'green lighten-4'
					} else if (index >= 1) {
						color = 'green lighten-5'
					}
				} else {
					if (index >= 6) {
						color = 'red lighten-2'
					} else if (index >= 5) {
						color = 'red lighten-3'
					} else if (index >= 4) {
						color = 'red lighten-4'
					} else if (index >= 1) {
						color = 'red lighten-5'
					}
				}
			}
			return color
		},

		statusColor(status) {
			let color = 'white lighten-4'
			if (status === 'TX_SUCCESS') {
				color = 'green lighten-4'
			} else if (status === 'TX_ERROR') {
				color = 'red lighten-4'
			} else if (status === 'NONE') {
				color = 'white lighten-4'
			} else if (status === 'SKIP') {
				color = 'amber lighten-4'
			}

			return color
		},

		upDownColor(item) {
			let color = 'grey lighten-4'
			if (item.up_down === 'UP') {
				color = 'green lighten-5'
			} else if (item.up_down === 'DOWN') {
				color = 'red lighten-5'
			} else if (item.up_down === 'BURN') {
				color = 'amber lighten-5'
			}
			return color
		},
		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},
		cToFixed(num, size) {
			try {
				return num.toFixed(size)
			} catch (e) {
				return ''
			}
		},
		cSubstr(txt, index) {
			let tResult = ''
			try {
				tResult = txt.substring(0, index).toLowerCase()
			} catch (e) {
				console.log('error')
			}
			return tResult
		},
	},
}
